import React, { useState, useEffect } from 'react';
import * as XLSX from 'xlsx';
import './App.css';
import FaultCodeExclusion from './FaultCodeExclusion';
import moment from 'moment';

const columns = ['Car No.', 'Detect', 'Clear', 'Fault Code', 'Fault Name', 'Fault Level', 'Speed (km/h)', 'Driving Mode'];

const filterColumns = ['Car No.', 'Detect', 'Clear', 'Fault Name', 'Fault Level'];

const FilterDropdown = ({ column, value, onChange, options }) => {
  if (column === 'Detect' || column === 'Clear') {
    return (
      <div className="filter-dropdown">
        <label>{column}</label>
        <input
          type="date"
          value={value ? moment(value, 'DD/MM/YYYY').format('YYYY-MM-DD') : ''}
          onChange={(e) => onChange(column, moment(e.target.value).format('DD/MM/YYYY'))}
        />
      </div>
    );
  }

  return (
    <div className="filter-dropdown">
      <label>{column}</label>
      <select value={value} onChange={(e) => onChange(column, e.target.value)}>
        <option value="">All</option>
        {options.map((option, index) => (
          <option key={index} value={option}>{option}</option>
        ))}
      </select>
    </div>
  );
};

const RowCountBox = ({ count }) => (
  <div className="row-count-box">
    <p>Total Rows: {count}</p>
  </div>
);

// Function to fix date format
const fixDateFormat = (dateString) => {
  if (!dateString) return '';
  
  // Try parsing with moment.js
  const formats = ['DD/MM/YYYY HH:mm', 'DD-MM-YYYY HH:mm:ss', 'M/D/YYYY HH:mm'];
  const parsedDate = moment(dateString, formats, true);
  
  if (parsedDate.isValid()) {
    return parsedDate.format('DD/MM/YYYY HH:mm');
  }

  // If moment.js parsing fails, try Excel serial number conversion
  const serialDate = parseFloat(dateString);
  if (!isNaN(serialDate)) {
    const date = new Date(Date.UTC(1899, 11, 30 + Math.floor(serialDate)));
    const fractionalDay = serialDate - Math.floor(serialDate);
    const totalMilliseconds = fractionalDay * 24 * 60 * 60 * 1000;
    date.setUTCMilliseconds(date.getUTCMilliseconds() + totalMilliseconds);
    return moment(date).format('DD/MM/YYYY HH:mm');
  }

  // If all parsing attempts fail, return the original string
  return dateString;
};

const TaskTable = ({ tasks, filters, excludedFaultCodes }) => {
  if (tasks.length === 0) return null;

  const filteredTasks = tasks.flatMap(fileData =>
    fileData.filter(task => {
      if (excludedFaultCodes.some(code =>
        task['Fault Code'].startsWith(code) ||
        task['Fault Code'] === code
      )) return false;

      return Object.entries(filters).every(([key, value]) => {
        if (value === '') return true;
        if (key === 'Detect' || key === 'Clear') {
          if (!task[key]) return false;
          const taskDate = moment(task[key], 'DD/MM/YYYY HH:mm');
          const filterDate = moment(value, 'DD/MM/YYYY');
          return taskDate.isSame(filterDate, 'day');
        }
        return String(task[key]).toLowerCase().includes(String(value).toLowerCase());
      });
    })
  );
  
  return (
    <>
      <RowCountBox count={filteredTasks.length} />
      <table>
        <thead>
          <tr>
            {columns.map((column, index) => (
              <th key={index}>{column}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {filteredTasks.map((task, rowIndex) => (
            <tr key={rowIndex}>
              {columns.map((column, colIndex) => (
                <td key={colIndex}>{task[column]}</td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </>
  );
};

const AlertTable = ({ alertData, onCountClick, excludedFaultCodes, filters }) => {
  const filteredAndSortedAlertData = [...alertData]
    .filter(alert => 
      !excludedFaultCodes.some(code => 
        alert['Fault Code'].startsWith(code) || 
        alert['Fault Code'] === code
      )
    )
    .filter(alert => {
      return Object.entries(filters).every(([key, value]) => {
        if (value === '') return true;
        if (key === 'Detect' || key === 'Clear') {
          // Alert table doesn't have Detect or Clear, so we skip these filters
          return true;
        }
        return String(alert[key]).toLowerCase().includes(String(value).toLowerCase());
      });
    })
    .sort((a, b) => {
      const aLevel = parseInt(a['Fault Level']) || a['Fault Level'];
      const bLevel = parseInt(b['Fault Level']) || b['Fault Level'];
      return aLevel < bLevel ? -1 : aLevel > bLevel ? 1 : 0;
    });

  return (
    <table className="alert-table">
      <thead>
        <tr>
          <th>Car No.</th>
          <th>Fault Code</th>
          <th>Fault Name</th>
          <th>Fault Level</th>
          <th>Count</th>
        </tr>
      </thead>
      <tbody>
        {filteredAndSortedAlertData.map((alert, index) => (
          <tr key={index}>
            <td>{alert['Car No.']}</td>
            <td>{alert['Fault Code']}</td>
            <td>{alert['Fault Name']}</td>
            <td>{alert['Fault Level']}</td>
            <td>
              <span 
                className="clickable-count"
                onClick={() => onCountClick(alert['Car No.'], alert['Fault Name'])}
              >
                {alert.Count}
              </span>
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  );
};

function Login({ onLogin }) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    // This is a simple authentication. In a real app, you'd want to use a secure backend.
    if (username === 'admin' && password === 'admin') {
      onLogin();
    } else {
      setError('Invalid username or password');
    }
  };

  return (
    <div className="login-container">
      <h2>Login to access TMS</h2>
      <form onSubmit={handleSubmit}>
        <div>
          <label>Username:</label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
          />
        </div>
        <div>
          <label>Password:</label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        {error && <p className="error">{error}</p>}
        <button type="submit">Login</button>
      </form>
    </div>
  );
}

function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [trainData, setTrainData] = useState({
    SP: { tasks: [], alertData: [] },
    C: { tasks: [], alertData: [] }
  });
  const [filters, setFilters] = useState({
    'Car No.': '',
    'Detect': '',
    'Clear': '',
    'Fault Name': '',
    'Fault Level': ''
    });

  const [showAlert, setShowAlert] = useState(false);
  const [selectedTrain, setSelectedTrain] = useState(null);
  const [excludedFaultCodes, setExcludedFaultCodes] = useState([]);

  useEffect(() => {
    const initialFilters = {};
    filterColumns.forEach(column => {
      initialFilters[column] = '';
    });
    setFilters(initialFilters);
  }, []);

  const handleCountClick = (carNo, faultName) => {
    setFilters(prevFilters => ({
      ...prevFilters,
      'Car No.': carNo,
      'Fault Name': faultName
    }));
    setShowAlert(false);
  };

  const handleResetFilters = () => {
    const initialFilters = {};
    filterColumns.forEach(column => {
      initialFilters[column] = '';
    });
    setFilters(initialFilters);
  };

  const handleExcludeFaultCodes = (codes) => {
    setExcludedFaultCodes(codes);
    // Reset the Fault Name filter when fault codes are excluded
    setFilters(prevFilters => ({
      ...prevFilters,
      'Fault Name': ''
    }));
  };

  const getAllFaultCodes = () => {
    return [...new Set(trainData[selectedTrain].tasks.flat().map(task => task['Fault Code']))];
  };

  const handleFileImport = (event) => {
    const files = Array.from(event.target.files);
    
    Promise.all(files.map(file => {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        
        reader.onload = (e) => {
          try {
            const data = new Uint8Array(e.target.result);
            const workbook = XLSX.read(data, { type: 'array' });
            const sheetName = workbook.SheetNames[0];
            const worksheet = workbook.Sheets[sheetName];
            const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1 });
        
            const headers = jsonData[0];
            const rawData = jsonData.slice(1);
        
            let fixedData;
            if (selectedTrain === 'SP') {
              fixedData = rawData
                .filter(row => !row.every(cell => cell === row[0]))
                .map((row, index) => ({
                  'No.': index + 1,
                  'Car No.': row[headers.indexOf('Car No.')],
                  'Detect': fixDateFormat(row[headers.indexOf('Detect')]),
                  'Clear': fixDateFormat(row[headers.indexOf('Clear')]),
                  'Fault Code': row[headers.indexOf('Fault Code')],
                  'Fault Name': row[headers.indexOf('Fault Name')],
                  'Fault Level': row[headers.indexOf('Fault Level')],
                  'Speed (km/h)': row[headers.indexOf('Speed (km/h)')],
                  'Driving Mode': row[headers.indexOf('Driving Mode')]
                }));
            } else if (selectedTrain === 'C') {
              fixedData = rawData
                .filter(row => !row.every(cell => cell === row[0]))
                .map((row, index) => ({
                  'No.': index + 1,
                  'Car No.': row[1],
                  'Detect': fixDateFormat(row[6]),
                  'Clear': fixDateFormat(row[7]),
                  'Fault Code': row[2],
                  'Fault Name': row[3],
                  'Fault Level': row[4],
                  'Speed (km/h)': row[9],
                  'Driving Mode': row[10]
                }));
            }
            
            resolve(fixedData);
          } catch (error) {
            reject(error);
          }
        };
        
        reader.onerror = (error) => reject(error);
        reader.readAsArrayBuffer(file);
      });
    }))
    .then(allFixedData => {
      setTrainData(prevData => {
        const existingData = prevData[selectedTrain].tasks.flat();
        const newUniqueData = allFixedData.flat().filter(newRow => 
          !existingData.some(existingRow => 
            Object.keys(newRow).every(key => 
              key === 'No.' || newRow[key] === existingRow[key]
            )
          )
        );
  
        const updatedTasks = [...prevData[selectedTrain].tasks, newUniqueData];
        const newAlertData = generateAlertData(updatedTasks.flat());
  
        return {
          ...prevData,
          [selectedTrain]: {
            tasks: updatedTasks,
            alertData: newAlertData
          }
        };
      });
    })
    .catch(error => {
      console.error("Error processing files:", error);
      // Handle error (e.g., show an error message to the user)
    });
  };

  const generateAlertData = (data) => {
    const alertCounts = {};
    data.forEach(task => {
      const key = `${task['Car No.']}-${task['Fault Code']}`;
      if (!alertCounts[key]) {
        alertCounts[key] = {
          'Car No.': task['Car No.'],
          'Fault Code': task['Fault Code'],
          'Fault Name': task['Fault Name'],
          'Fault Level': task['Fault Level'],
          Count: 0
        };
      }
      alertCounts[key].Count++;
    });
  
    return Object.values(alertCounts).filter(alert => alert.Count >= 20);
  };

  const handleFilterChange = (column, value) => {
    setFilters(prevFilters => ({
      ...prevFilters,
      [column]: value
    }));
  };

  const getUniqueValues = (tasks, column) => {
    const values = [...new Set(tasks.flat().map(task => task[column] || ''))].filter(Boolean);
    
    if (column === 'Fault Name') {
      // Filter out fault names associated with excluded fault codes
      return values.filter(faultName => {
        const associatedTask = tasks.flat().find(task => task['Fault Name'] === faultName);
        return associatedTask && !excludedFaultCodes.some(code => 
          associatedTask['Fault Code'].startsWith(code) || 
          associatedTask['Fault Code'] === code
        );
      });
    }
    
    return values.sort((a, b) => {
      if (typeof a === 'number' && typeof b === 'number') {
        return a - b;
      } else {
        return String(a).localeCompare(String(b), undefined, { numeric: true, sensitivity: 'base' });
      }
    });
  };

  if (!isLoggedIn) {
    return <Login onLogin={() => setIsLoggedIn(true)} />;
  }

  return (
    <div className='App'>
      <h1>Train Monitor System TMS</h1>
      {!selectedTrain ? (
        <div className="train-selection">
          <button className="train-button" onClick={() => setSelectedTrain('SP')}>SP Train</button>
          <button className="train-button" onClick={() => setSelectedTrain('C')}>C Train</button>
        </div>
      ) : (
        <>
          <h2>{selectedTrain} Train Data</h2>
          <div className="import-container">
            <input
              type="file"
              accept=".xlsx, .xls"
              onChange={handleFileImport}
              style={{ display: 'none' }}
              id="fileInput"
              multiple
            />
            <label htmlFor="fileInput" className="import-button">
              Import from Excel
            </label>
            <button className="alert-button" onClick={() => setShowAlert(!showAlert)}>
              {showAlert ? 'Hide Alerts' : 'Show Alerts'}
            </button>
            <FaultCodeExclusion 
              faultCodes={getAllFaultCodes()} 
              onExclude={handleExcludeFaultCodes} 
            />
            <button className="back-button" onClick={() => {
              setSelectedTrain(null);
              setShowAlert(false);
            }}>
              Back to Train Selection
            </button>
            <button className="logout-button" onClick={() => setIsLoggedIn(false)}>
              Logout
            </button>
          </div>

          <div className="filters">
        {filterColumns.map(column => (
          <FilterDropdown
            key={column}
            column={column}
            value={filters[column]}
            onChange={handleFilterChange}
            options={getUniqueValues(trainData[selectedTrain].tasks, column)}
          />
        ))}
            <button className="reset-button" onClick={handleResetFilters}>
              Reset Filters
            </button>
          </div>

          {showAlert && (
            <AlertTable 
              alertData={trainData[selectedTrain].alertData} 
              onCountClick={handleCountClick}
              excludedFaultCodes={excludedFaultCodes}
              filters={filters}
            />
          )}
          
          <div className="table-container">
            <TaskTable 
              tasks={trainData[selectedTrain].tasks} 
              filters={filters} 
              excludedFaultCodes={excludedFaultCodes}
            />
          </div>
        </>
      )}
    </div>
  );
}

export default App;