import React, { useState, useEffect } from 'react';
import './FaultCodeExclusion.css';

const FaultCodeExclusion = ({ faultCodes, onExclude }) => {
  const [excludedCodes, setExcludedCodes] = useState([]);
  const [isOpen, setIsOpen] = useState(false);

  const groupedFaultCodes = faultCodes.reduce((acc, code) => {
    const group = code.match(/^[A-Z]+/)[0];
    if (!acc[group]) acc[group] = [];
    acc[group].push(code);
    return acc;
  }, {});

  // Sort the fault codes within each group
  Object.keys(groupedFaultCodes).forEach(group => {
    groupedFaultCodes[group].sort((a, b) => a.localeCompare(b, undefined, { numeric: true, sensitivity: 'base' }));
  });

  // Sort the groups alphabetically
  const sortedGroups = Object.keys(groupedFaultCodes).sort();

  const handleExclude = (code) => {
    setExcludedCodes(prev => {
      const newExcludedCodes = prev.includes(code)
        ? prev.filter(c => c !== code)
        : [...prev, code];
      onExclude(newExcludedCodes);
      return newExcludedCodes;
    });
  };

  const isGroupExcluded = (group) => {
    return groupedFaultCodes[group].every(code => excludedCodes.includes(code));
  };

  const handleGroupExclude = (group) => {
    setExcludedCodes(prev => {
      const groupCodes = groupedFaultCodes[group];
      const newExcludedCodes = isGroupExcluded(group)
        ? prev.filter(code => !groupCodes.includes(code))
        : [...new Set([...prev, ...groupCodes])];
      onExclude(newExcludedCodes);
      return newExcludedCodes;
    });
  };

  return (
    <div className="fault-code-exclusion">
      <button className="exclude-faults-btn" onClick={() => setIsOpen(true)}>Exclude Faults</button>
      {isOpen && (
        <div className="popup-overlay">
          <div className="popup-content">
            <h2>Choose to Exclude</h2>
            <div className="fault-groups">
              {sortedGroups.map(group => (
                <div key={group} className="fault-group">
                  <div className="group-checkbox">
                    <input
                      type="checkbox"
                      id={group}
                      checked={isGroupExcluded(group)}
                      onChange={() => handleGroupExclude(group)}
                    />
                    <label htmlFor={group}>{group}</label>
                  </div>
                  <div className="code-checkboxes">
                    {groupedFaultCodes[group].map(code => (
                      <div key={code} className="code-checkbox">
                        <input
                          type="checkbox"
                          id={code}
                          checked={excludedCodes.includes(code)}
                          onChange={() => handleExclude(code)}
                        />
                        <label htmlFor={code}>{code}</label>
                      </div>
                    ))}
                  </div>
                </div>
              ))}
            </div>
            <button className="exclude-faults-btn" onClick={() => setIsOpen(false)}>Close</button>
          </div>
        </div>
      )}
    </div>
  );
};

export default FaultCodeExclusion;